// Opera 8.0+
declare var opr: any;
const isOpera =
    // tslint:disable-next-line:no-unsafe-any
    (!!(window as any).opr && !!opr.addons) || !!(window as any).opera || navigator.userAgent.indexOf(" OPR/") >= 0;

// Firefox 1.0+
declare var InstallTrigger: any;
const isFirefox = typeof InstallTrigger !== "undefined";

// Safari 3.0+ "[object HTMLElementConstructor]"
declare var safari: any;
const isSafari =
    // tslint:disable-next-line:no-unsafe-any
    /constructor/i.test((window as any).HTMLElement) ||
    // tslint:disable-next-line:only-arrow-functions no-unsafe-any
    (function(p) { return p.toString() === "[object SafariRemoteNotification]"; })(
        // tslint:disable-next-line:no-string-literal no-unsafe-any
        !((window as any)["safari"]) || (typeof safari !== "undefined" && safari.pushNotification));

// Internet Explorer 6-11
// tslint:disable-next-line:binary-expression-operand-order
const isIE = /*@cc_on!@*/false || !!(document as any).documentMode;

// Edge 20+
const isEdge = !isIE && !!(window as any).StyleMedia;

// Chrome 1+
// tslint:disable-next-line:no-unsafe-any
const isChrome = !!(window as any).chrome && !!(window as any).chrome.webstore;

// Blink engine detection
const isBlink = (isChrome || isOpera) && !!(window as any).CSS;

if (true) {
    const appElement = document.getElementById("app");

    if (appElement) {
        appElement.innerHTML = `
            <div style="font-size:large">
                <p><strong>Asset Manager doesn't work on this browser.</strong></p>
                <p>
                    It is recommended to use Asset Manager from within a recent version of an
                    <strong>open-source</strong> browser like <strong>Chromium</strong> or <strong>Firefox</strong>.
                </p>
                <p>
                    Asset Manager should also work on recent incarnations of proprietary browsers like Chrome, Opera,
                    Safari, Edge and probably others.
                </p>
                <p>(${window.navigator.userAgent})</p>
                <p>isOpera: ${isOpera}</p>
                <p>isFirefox: ${isFirefox}</p>
                <p>isSafari: ${isSafari}</p>
                <p>isIE: ${isIE}</p>
                <p>isEdge: ${isEdge}</p>
                <p>isChrome: ${isChrome}</p>
                <p>isBlink: ${isBlink}</p>
            </div>
        `;
    }
} else {
    // tslint:disable-next-line:no-require-imports no-var-requires
    require("./app");
}
